.line {
    transition: all .3s;
}

.line:hover {
    stroke: #3b3f41;
}

.lineWrapper {
    cursor: pointer;
    transition: all .3s;
}

.circleWrapper {
    cursor: pointer;
    transition: all .3s;
}

.circleWrapper:hover circle {
    stroke: #3b3f41;
}

.lineWrapper:hover circle {
    fill: #3b3f41;
}

.lineWrapper:hover .line {
    stroke: #3b3f41;
}
